body {
  --color-blue: #00acdc;
  --color-black: #000;
  --header-height: 60px;
  margin: 0;
}

html {
  color: var(--color-black);
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 500;
}

* {
  box-sizing: border-box;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  align-items: center;
  width: 40px;
  height: 40px;
  display: flex;
}

.nav__link {
  color: var(--color-black);
  font-size: 12px;
  line-height: var(--header-height);
  text-decoration: none;
  display: block;
  position: relative;
}

.nav__list {
  text-transform: uppercase;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-right: 20px;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link:hover {
  color: var(--color-blue);
}

.nav__link.is-active {
  color: var(--color-blue);
  position: relative;
}

.nav__link.is-active:after {
  content: "";
  background: var(--color-blue);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
